<template>
    <div>
        <div class="content">
            <div class="box" id="box">
                <div class="in-bottom">
                    
                </div>
                <div class="in in-left">
                    <span @click="openWrap">
                        <img style="max-width:100%;max-width:100%;" src="@/img/sb1.jpg" alt="">
                    </span>
                </div>
                <div class="in in-top">
                    <div class="in in-top-left velocity-animating">
                        <!-- <img style="max-width:100%;max-width:100%;" src="@/img/ws.png" alt=""> -->
                    </div>
                    <div class="in in-top-right velocity-animating">
                        <!-- <img style="max-width:100%;max-width:100%;" src="@/img/db.png" alt=""> -->
                    </div>
                </div>
                <div class="in in-right">
                    <span>
                        <img style="max-width:100%;max-width:100%;" src="@/img/mb.png" alt="">
                    </span>
                </div>
                <div class="in in-front">
                    <span>
                        <img style="max-width:100%;max-width:100%;" src="@/img/dd.png" alt="">
                    </span>
                </div>
                <div class="in in-behin">
                    <span>
                        <img style="max-width:100%;max-width:100%;" src="@/img/bq.png" alt="">
                    </span>
                </div>
                <div class="bed">
                    <div class="bed-bottom currency"></div>
                    <div class="bed-left currency" ></div>
                    <div class="bed-top currency">
                        <div class="img-wrap">
                            <div class="img">
                                
                            </div>
                        </div>
                    </div>
                    <div class="bed-right currency"></div>
                    <div class="bed-front currency"></div>
                    <div class="bed-behin currency"></div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
  name: "three",
  data() {
    return {
      obj:{ 
          x:20, //列
          y:5, //层
          z:5, //行
      },
      boxWidth:30, //容器长度单位
      RotateY:10,
      RotateX:-10,
      old_x:0,
      old_y:0,
      num:0,
      flag:false,
      isVelocity:true,
      boxStyle:'',
      maxTemperature:35,
      minTemperature:10,
      timer:null,
      form:{
          x:'',
          y:'',
          z:'',
          type:"2"
      },
      color:["#C00000",'#f74015','#1082f9']
    };
  },
  created(){
      
    String.prototype.colorRgb = function(){
        // 16进制颜色值的正则
        var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
        // 把颜色值变成小写
        var color = this.toLowerCase();
        if (reg.test(color)) {
            // 如果只有三位的值，需变成六位，如：#fff => #ffffff
            if (color.length === 4) {
            var colorNew = "#";
            for (var i = 1; i < 4; i += 1) {
                colorNew += color.slice(i, i + 1).concat(color.slice(i, i + 1));
            }
            color = colorNew;
            }
            // 处理六位的颜色值，转为RGB
            var colorChange = [];
            for (var i = 1; i < 7; i += 2) {
            colorChange.push(parseInt("0x" + color.slice(i, i + 2)));
            }
            return "RGBA(" + colorChange.join(",") + ",0.2)";
        } else {
            return color;
        }
    }

  },
  mounted() {

        let _this = this;
        let obj = this.obj;
        $("body").append("<div style='width:100%;height:100%;background:#666;position:fixed;left: 0;top: 0;z-index: -1;'></div>")
        $(".box").css({
            width:_this.boxWidth*obj.x,
            height:_this.boxWidth*obj.y
        });
        $('.box .in-bottom').css({
            width:_this.boxWidth*obj.x,
            height:_this.boxWidth*obj.z,
            transform: `rotateX(-90deg) translateZ(${_this.boxWidth*obj.y/2}px)`
        });
        $('.box .in-left').css({
            width:_this.boxWidth*obj.z,
            height:_this.boxWidth*obj.y,
            transform: `translateX(-${_this.boxWidth*obj.z/2}px) rotateY(-90deg)`
        });
        $('.box .in-front').css({
            width:_this.boxWidth*obj.x,
            height:_this.boxWidth*obj.y,
            transform: `translateZ(-${_this.boxWidth*obj.z/2}px)`
        });
        $('.box .in-top').css({
            width:_this.boxWidth*obj.x,
            height:_this.boxWidth*obj.z,
            transform: `rotateX(-90deg) translateZ(-${_this.boxWidth*obj.y/2}px)`,
        });
        $('.box .in-top-left').css({
            width:_this.boxWidth*obj.x,
            height:(2/Math.sqrt(3))*_this.boxWidth*obj.z/2,
            // height:_this.boxWidth*obj.z/2,
            transformOrigin:"0 100%",
            transform: `rotateX(30deg)`
        });
        $('.box .in-top-right').css({
            width:_this.boxWidth*obj.x,
            height:(2/Math.sqrt(3))*_this.boxWidth*obj.z/2,
            // height:_this.boxWidth*obj.z/2,
            transformOrigin:"0 0",
            transform: `rotateX(-30deg)`
        });
        $('.box .in-right').css({
            width:_this.boxWidth*obj.z,
            height:_this.boxWidth*obj.y,
            transform: `translateX(${_this.boxWidth*(obj.x-obj.z/2)}px) rotateY(-90deg)`
        });
         $('.box .in-behin').css({
            width:_this.boxWidth*obj.x,
            height:_this.boxWidth*obj.y,
            transform: `translateZ(${_this.boxWidth*obj.z/2}px)`
        });
        $(".box .bed").css({
            width:_this.boxWidth*obj.x-30,
            height:(_this.boxWidth*obj.y-30)/2,
            left:15,
            bottom:0,
        });
         $('.box .bed .bed-bottom').css({
            width:_this.boxWidth*obj.x-30,
            height:_this.boxWidth*obj.y-30,
            transform: `rotateX(-90deg)`
        });
        $('.box .bed .bed-left').css({
            width:_this.boxWidth*obj.z-30,
            height:(_this.boxWidth*obj.z-30)/2,
            transform: `translateX(-${(_this.boxWidth*obj.z-30)/2}px) rotateY(-90deg)`
        });
        $('.box .bed .bed-front').css({
            width:_this.boxWidth*obj.x-30,
            height:(_this.boxWidth*obj.y-30)/2,
            transform: `translateZ(-${(_this.boxWidth*obj.z-30)/2}px)`
        });
        $('.box .bed .bed-top').css({
            width:_this.boxWidth*obj.x-30,
            height:_this.boxWidth*obj.z-30,
            transform: `rotateX(-90deg) translateZ(-${((_this.boxWidth*obj.z-30)/2)}px)`
        });
        $('.box .bed .bed-top .img-wrap').css({
            width:_this.boxWidth*obj.z-30,
            height:_this.boxWidth*obj.z-30,
            // transform: `rotateZ(-90deg) translateY(-${((_this.boxWidth*obj.x-30)/2-90)}px) translateX(15px)`
        });
        let imgHeight = parseInt($('.box .bed .bed-top .img-wrap .img').width()/(120/151.641))
        // $('.box .bed .bed-top .img-wrap img').css({
        //     transform: `rotateZ(-90deg) translateX(${(imgHeight-(_this.boxWidth*obj.z-30/2))}px) translateY(${(imgHeight-(_this.boxWidth*obj.z-30/2))}px)`
        // });
         this.velocity($('.box .bed .bed-top .img-wrap .img')[0],{
            height:imgHeight,
            rotateZ:'-90deg',
            translateX:`${(imgHeight-(_this.boxWidth*obj.z-30/2))}px`,
            translateY:`${(imgHeight-(_this.boxWidth*obj.z-30/2))}px`
        })
        $('.box .bed .bed-right').css({
            width:_this.boxWidth*obj.z-30,
            height:(_this.boxWidth*obj.y-30)/2,
            transform: `rotateY(90deg) translateZ(${(_this.boxWidth*obj.x-30)-(_this.boxWidth*obj.z-30)/2}px)`
        });
         $('.box .bed .bed-behin').css({
            width:_this.boxWidth*obj.x-30,
            height:(_this.boxWidth*obj.y-30)/2,
            transform: `translateZ(${(_this.boxWidth*obj.z-30)/2}px)`
        });
      $(".content")[0].onmousedown = function (e) {
        _this.flag = true;
        _this.old_x = e.pageX;
        _this.old_y = e.pageY;
        //console.log(old_x, old_y);
        /*原本所处的位置*/
      };
      $(".content")[0].onmouseup = function () {
        _this.flag = false;
        //鼠标抬起时结束
      };
      $(".content")[0].onmousemove = function (e) {
        if (_this.flag) {
          var _x = e.pageX - _this.old_x;
          var _y = e.pageY - _this.old_y;
          /*新位置减去老位置*/
          _this.RotateY += _x / 400;
          _this.RotateX += -(_y / 400);
          $('.box').css({
            transition: 'linear',
            transform: 'rotateX(' + _this.RotateX + 'deg) rotateY(' + _this.RotateY + 'deg) scale('+(1+_this.num*0.01) +')'
          });
        }
      };
      var scroolFun = (event)=>{
          event = event || window.event;
          var wheel = event.wheelDelta || event.detail;
          if(wheel<0){
              _this.num--;
          }else{
              _this.num++;
          }
          $(".box").css({
              transform:'rotateX(' + _this.RotateX + 'deg) rotateY(' + _this.RotateY + 'deg) scale('+(1+_this.num*0.01) +')'
          })
          return false;
      };
      $(".content")[0].addEventListener('DOMMouseScroll',scroolFun,false);
      $(".content")[0].onmousewheel=scroolFun;
      let that = this;
      setInterval(()=>{
          $(".content").css("background-color",that.randomHexColor())
      },500)
      this.timer = setInterval(()=>{
          $(".bed div").css("background-color",this.randomHexColor())
          $(".in").each(function(){
              $(this).css("background-color",that.randomHexColor().colorRgb())
          })
      },2000)
  },
  methods: {
    randomHexColor() { //随机生成十六进制颜色
        let hex = Math.floor(Math.random() * 16777216).toString(16); //生成ffffff以内16进制数
        while (hex.length < 6) { //while循环判断hex位数，少于6位前面加0凑够6位
            hex = '0' + hex;
        }
        return '#' + hex; //返回‘#'开头16进制颜色
    },
    openWrap(){
        let _this = this
        let obj = this.obj
       this.velocity($('.box .in-top-left')[0],{
            rotateX:['120deg','30deg'],
        },{
            duration:3000,
            // loop: 1,
            complete:(elements)=>{
                let imgHeight = $('.box .bed .bed-top .img-wrap img').height()
                // this.velocity($('.box .bed .bed-top .img-wrap')[0],{
                //     rotateZ:["180deg"],
                // },{
                //     duration:3000,
                //     complete:(elements)=>{
                //         this.velocity($('.box .bed .bed-top .img-wrap .img')[0],{
                //             translateZ:["-=100px"],
                //         },{
                //             duration:3000,
                //         })
                //     }
                // })
            }
        });
        this.velocity($('.box .in-top-right')[0],{
            rotateX:['-120deg','-30deg'],
        },{
            duration:3000,
            // loop: 1,
            complete:(elements)=>{

            }
        });
    }

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    .content {
        padding-top: 300px;
        perspective: 1000; /* 视距 */
        background:#666;
        height: 1000px;
    }
    
    .box {
        position: relative;
        margin: 0 auto;
        color: #fffdf5;
        font-size: 36px;
        font-weight: bold;
        text-align: center;
        transform-style: preserve-3d; /* 默认 2D */
        /*不让鼠标选中文字*/
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transform: rotateZ(0deg) rotateX(0deg) rotateY(0deg);
    }

    .box .in-behin {
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        position: absolute;
        transform-style: preserve-3d; /* 默认 2D */
        background-color: rgba(249,204,157,0.2);
    }
    .box .in-behin span{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        width: 50px;
        height: 50px;
        /* color:rgb(3, 1, 1);
        background: brown; */
        background: #666;
        color: #fffdf5;
        line-height: 50px;
    }
    .box .in-front {
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        transform-style: preserve-3d; /* 默认 2D */
        background-color: rgba(249,204,157,0.2);
    }
    .box .in-front span{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%) rotateY(180deg);
        width: 50px;
        height: 50px;
        /* color:rgb(3, 1, 1);
        background: brown; */
        background: #666;
        color: #fffdf5;
        line-height: 50px;
    }
    
    .box .in-back {
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        transform-style: preserve-3d; /* 默认 2D */
        background-color: rgba(249,204,157,0.2);
    }
    
    .box .in-left {
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        transform-style: preserve-3d; /* 默认 2D */
        background-color: rgba(249,204,157,0.2);
    }
    .box .in-left span{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%);
        width: 50px;
        height: 50px;
        /* color:rgb(3, 1, 1);
        background: brown; */
        background: #666;
        color: #fffdf5;
        line-height: 50px;
    }
    
    .box .in-right {
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        background-color: rgba(249,204,157,0.2);
        /* transform: translateX(10px) rotateY(90deg); */
    }
    .box .in-right span{
        position: absolute;
        left:50%;
        top:50%;
        transform: translate(-50%,-50%) rotateY(180deg);
        width: 50px;
        height: 50px;
        /* color:rgb(3, 1, 1);
        background: brown; */
        background: #666;
        color: #fffdf5;
        line-height: 50px;
    }
    .box .in-top {
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        background-color: rgba(249,204,157,0.2);
        transform-style: preserve-3d; /* 默认 2D */
    }
    .box .in-top-left {
        width: 100%;
        height: 100%;
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        background-color: rgba(249,204,157,0.2);
        left:0;
        bottom:0;
    }
    .box .in-top-right {
        width: 100%;
        height: 100%;
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        background-color: rgba(249,204,157,0.2);
    }
    .box .in-bottom {
        position: absolute;
        border: 1px solid rgba(249,204,157,0.8);
        box-sizing: border-box;
        background-color: rgba(249,204,157,0.2);
    }
    .box .bed{
        position: absolute;
        text-align: center;
        transform-style: preserve-3d; /* 默认 2D */
        /*不让鼠标选中文字*/
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transform: rotateZ(0deg) rotateX(0deg) rotateY(0deg);
    }
    .box .bed .currency{
        position: absolute;
        box-sizing: border-box;
        background-color: rgb(7, 5, 2);
    }
 
    .box .bed .bed-top .img-wrap{
        transform-style: preserve-3d; /* 默认 2D */
        left:30px;
        bottom:0;
    }
    .box .bed .bed-top .img-wrap .img{
        width: 100%;
        left:0;
        top:0;
        background-color: red;
        background: url(./../img/sb.jpg) no-repeat 0 0;
        background-size:100% 100%;
    }
</style>



