<template>
    <div>
        <div style="height:10px;"></div>
        <el-form :inline="true" :model="formInline" class="demo-form-inline" style="padding-left:20px;">
            <el-form-item label="仓房数量">
                <el-input v-model="formInline.number" placeholder="请输入0到28之间的整数"></el-input>
            </el-form-item>
            <el-form-item label="容器款度">
                <el-input v-model="formInline.width" placeholder="请输入容器宽度"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="restWrap">查看</el-button>
            </el-form-item>
        </el-form>
        <div class="content" ref="content" style="margin:10px auto;">
            <img src="./../img/store-bg.jpg" alt="" class="bg">
            <div class="store-house" v-for="item in storeObj.number" :key="item" :index="item"
            :style="'width:'+ storeConfigList[storeObj.index].width+'px;height:'+ storeConfigList[storeObj.index].height +'px;left:'
            + (((storeConfigList[storeObj.index].left)+(Math.floor(item/(storeObj.column+0.1))*storeConfigList[storeObj.index].leftDifValueRootNum))+ ((storeConfigList[storeObj.index].leftDifValueColumn)*((item-1)%storeObj.column))) +'px;top:'
            + (((storeConfigList[storeObj.index].top)+(Math.floor(item/(storeObj.column+0.1))*storeConfigList[storeObj.index].topDifValueRootNum)) - ((storeConfigList[storeObj.index].topDifValueColumn)*((item-1)%storeObj.column))) +'px'"
            >
                <div style="position:absolute;height:50%;z-index:1;width:100%;top:25%;" @click="storeClick(item)"></div>
                <img src="../img/store-normal.png" alt="">
            </div>
        </div>
    </div>
</template>
<script>

export default {
  name: "three",
  data() {
    return {
      obj:{ 
          x:20, //列
          y:5, //层
          z:5, //行
      },
      formInline:{
        number:1,
        width:1000
      },
      zoomRatio:1,
      bgObj:{ //背景图信息
          width:1920,
          height:1081
      },
      storeObj:{ //仓库信息
          number:15, //数量
          data:[], //数据
          column:3,//列
          index:2,//对应的配置列表下标
      },
      storeConfigList:[{
        column:1, //列
        rootNum:2,//最大行
        left:289, //最左边的仓库位置基数
        top:234,//最左边的仓库位置基数
        leftDifValueColumn:0, //仓与仓之间的距离差值
        topDifValueColumn:0, //仓与仓之间的距离差值
        leftDifValueRootNum:582, //同行仓与仓之间的距离差值
        topDifValueRootNum:261, //同行仓与仓之间的距离差值
        width:636,
        height:366,
      },{
        column:2, //列
        rootNum:3,//最大行
        left:177, //最左边的仓库位置基数
        top:302,//最左边的仓库位置基数
        leftDifValueColumn:165, //仓与仓之间的距离差值
        topDifValueColumn:109, //仓与仓之间的距离差值
        leftDifValueRootNum:365, //同行仓与仓之间的距离差值
        topDifValueRootNum:160, //同行仓与仓之间的距离差值
        width:420,
        height:237,
      },{
        column:3, //列
        rootNum:5,//最大行
        left:181, //最左边的仓库位置基数
        top:331,//最左边的仓库位置基数
        leftDifValueColumn:96, //仓与仓之间的距离差值
        topDifValueColumn:71, //仓与仓之间的距离差值
        leftDifValueRootNum:210, //同行仓与仓之间的距离差值
        topDifValueRootNum:95, //同行仓与仓之间的距离差值
        width:228,
        height:132,
      },{
        column:4, //列
        rootNum:7,//最大行
        left:182, //最左边的仓库位置基数
        top:352,//最左边的仓库位置基数
        leftDifValueColumn:74, //同列仓与仓之间的距离差值
        topDifValueColumn:59, //同列仓与仓之间的距离差值
        leftDifValueRootNum:156, //同行仓与仓之间的距离差值
        topDifValueRootNum:72, //同行仓与仓之间的距离差值
        width:167,
        height:98,
      }],
      boxWidth:30, //容器长度单位
      RotateY:10,
      RotateX:-10,
      old_x:0,
      old_y:0,
      num:0,
      flag:false,
      boxStyle:'',
      maxTemperature:35,
      minTemperature:10,
      form:{
          x:'',
          y:'',
          z:'',
          type:"2"
      },
      color:["#C00000",'#f74015','#1082f9']
    };
  },
  created(){

  },
  mounted() {
      this.restWrap()
  
  },
  methods: {
    restWrap(){
       $(this.$refs.content).width(this.formInline.width)
       this.zoomRatio = $(this.$refs.content).width()/1920
       $(this.$refs.content).height(1081*this.zoomRatio)
       this.storeConfigList=[{
        column:1, //列
        rootNum:2,//最大行
        left:289*this.zoomRatio, //最左边的仓库位置基数
        top:234*this.zoomRatio,//最左边的仓库位置基数
        leftDifValueColumn:0, //仓与仓之间的距离差值
        topDifValueColumn:0, //仓与仓之间的距离差值
        leftDifValueRootNum:582*this.zoomRatio, //同行仓与仓之间的距离差值
        topDifValueRootNum:261*this.zoomRatio, //同行仓与仓之间的距离差值
        width:636*this.zoomRatio,
        height:366*this.zoomRatio,
      },{
        column:2, //列
        rootNum:3,//最大行
        left:177*this.zoomRatio, //最左边的仓库位置基数
        top:302*this.zoomRatio,//最左边的仓库位置基数
        leftDifValueColumn:165*this.zoomRatio, //仓与仓之间的距离差值
        topDifValueColumn:109*this.zoomRatio, //仓与仓之间的距离差值
        leftDifValueRootNum:365*this.zoomRatio, //同行仓与仓之间的距离差值
        topDifValueRootNum:160*this.zoomRatio, //同行仓与仓之间的距离差值
        width:420*this.zoomRatio,
        height:237*this.zoomRatio,
      },{
        column:3, //列
        rootNum:5,//最大行
        left:181*this.zoomRatio, //最左边的仓库位置基数
        top:331*this.zoomRatio,//最左边的仓库位置基数
        leftDifValueColumn:96*this.zoomRatio, //仓与仓之间的距离差值
        topDifValueColumn:71*this.zoomRatio, //仓与仓之间的距离差值
        leftDifValueRootNum:210*this.zoomRatio, //同行仓与仓之间的距离差值
        topDifValueRootNum:95*this.zoomRatio, //同行仓与仓之间的距离差值
        width:228*this.zoomRatio,
        height:132*this.zoomRatio,
      },{
        column:4, //列
        rootNum:7,//最大行
        left:182*this.zoomRatio, //最左边的仓库位置基数
        top:352*this.zoomRatio,//最左边的仓库位置基数
        leftDifValueColumn:74*this.zoomRatio, //同列仓与仓之间的距离差值
        topDifValueColumn:59*this.zoomRatio, //同列仓与仓之间的距离差值
        leftDifValueRootNum:156*this.zoomRatio, //同行仓与仓之间的距离差值
        topDifValueRootNum:72*this.zoomRatio, //同行仓与仓之间的距离差值
        width:167*this.zoomRatio,
        height:98*this.zoomRatio,
      }]
       this.storeObj.number = parseInt(this.formInline.number)
       if(this.storeObj.number>15){
           this.storeObj.column = 4;
           this.storeObj.index = 3
       }else if(this.storeObj.number<=15 && this.storeObj.number>6){
            this.storeObj.column = 3;
            this.storeObj.index = 2
       }else if(this.storeObj.number<=6 && this.storeObj.number>2){
            this.storeObj.column = 2;
            this.storeObj.index = 1
       }else{
            this.storeObj.column = 1;
            this.storeObj.index = 0
       }
    },
    storeClick(item){
        alert(item)
    }

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .content {
        width: 1000px;
        margin-top:100px;
        position: relative;
    }
    .content .bg{
        display: block;
        width: 100%;
    }
    .content .store-house{
        position: absolute;
    }
    .content .store-house img{
        width: 100%;
        
    }
</style>



