<template>
    <div>
        <el-form ref="form" :model="form" label-width="150px" style="background:#fff;">
            <el-form-item label="选择列：">
                <el-checkbox-group v-model="form.x">
                    <el-checkbox v-for="item in obj.x+1" :label="item" :key="'lie' + item">第{{item}}列</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="选择行：">
                <el-checkbox-group v-model="form.z">
                    <el-checkbox v-for="item in obj.z+1" :label="item" :key="'hang' + item">第{{item}}行</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="选择层：">
                <el-checkbox-group v-model="form.y">
                    <el-checkbox v-for="item in obj.y" :label="item" :key="'ceng' + item">第{{item}}层</el-checkbox>
                </el-checkbox-group>
            </el-form-item>
            <el-form-item label="展示方式：">
                <el-radio-group v-model="form.type">
                    <el-radio label="0">整仓</el-radio>
                    <el-radio label="1">周边</el-radio>
                    <el-radio label="2">堆内</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
                <!-- <el-button type="primary" @click="onSubmit">查看</el-button> -->
            </el-form-item>
            </el-form>
        <div class="content">
            <div class="box">
                <div class="in-middle">
                    
                </div>
                <div class="in-left">
                    
                </div>
                <div class="in-top">
                    
                </div>
                <!-- <div class="head1">

                </div> -->
                <!-- <div class="head2">

                </div> -->
                <div id="tit">
                    <span class="tit" v-for="v in obj.arr" :key="'dian'+v.x+v.y+v.z"
                    v-show="((form.x.length<=0 && form.y.length<=0 && form.z.length<=0) || (form.x.indexOf(v.x)>-1) || (form.z.indexOf(v.z)>-1) || (form.y.indexOf(v.y)>-1))"
                    :x="v.x" :y="v.y" :z="v.z" :style="'background-color:'+temperatureFun(v.value)+';left:' + ((v.x-1)*boxWidth-15) +'px;transform:rotateX(90deg) translateY('+((v.z-1)*boxWidth-boxWidth*obj.z/2)+'px) translateZ('+((v.y-1)*boxWidth)+'px);'">{{v.value}}</span>
                </div>
                <div id="con">
                    <span class="line" v-for="v in obj.arr" :key="'xian'+v.x+v.y+v.z" v-if="v.y > 1" v-show="(form.x.length<=0 && form.y.length<=0 && form.z.length<=0) || (form.x.indexOf(v.x)>-1) || (form.z.indexOf(v.z)>-1) || (form.y.indexOf(v.y)>-1) && (form.y.indexOf(v.y-1)>=0)" :style="'left:' + ((v.x-1)*boxWidth) +'px;transform:translateY('+(-(v.y-2)*boxWidth)+'px) translateZ('+((v.z-1)*boxWidth-boxWidth*obj.z/2)+'px);'"></span>
                    <span class="con" v-for="v in obj.arr" v-if="v.x<=obj.x && v.y<=obj.y && v.z<=obj.z" v-show="(form.x.length<=0 && form.y.length<=0 && form.z.length<=0) || form.y.indexOf(v.y)>-1" :key="v.x+v.y+v.z" :style="'background-color:'+temperatureFun1(conValue(v.x,v.y,v.z))+';left:'+((v.x-1)*boxWidth)+'px;transform:rotateX(90deg) translateY('+((v.z-2)*boxWidth-boxWidth)+'px) translateZ('+((v.y-1)*boxWidth-boxWidth/2)+'px);'">{{conValue(v.x,v.y,v.z)}}</span>
                </div>

                
            </div>
        </div>
    </div>
</template>
<script>

export default {
  name: "three",
  data() {
    return {
      obj:{ 
          x:10, //列
          y:3, //层
          z:3, //行
          arr:[]
      },
      boxWidth:100, //容器长度单位
      RotateY:10,
      RotateX:-10,
      old_x:0,
      old_y:0,
      num:0,
      flag:false,
      boxStyle:'',
      form:{
          x:[],
          y:[],
          z:[],
          type:"1"
      },
      color:['#18DADC', '#14F2E5', '#94F8F7', '#FCFE83', '#F8DD4E', '#FEBC28', '#FF8810', '#F85208', '#E80000', '#C00000']
    };
  },
  created(){
        
  },
  mounted() {
    // const axios = require('axios');

    // axios.get('http://39.100.86.127:7800/api/temperature/echarts').then((response) => {
      
    // })
    let _this = this;
    $("body").append("<div style='width:100%;height:100%;background:#666;position:fixed;left: 0;top: 0;z-index: -1;'></div>")
    const axios = require('axios');
        axios.get('http://39.100.86.127:7800/api/temperature/echarts').then((response) => {
        this.data = response.data.data;
        this.obj.z = response.data.data.length;
        this.obj.x = response.data.data[0][response.data.data[0].length-1].channel;
        this.obj.y = response.data.data[0][response.data.data[0].length-1].rootNum;
        let x = 0;
        this.obj.arr = [];
        this.boxWidth = parseInt(1000/this.obj.x);
        response.data.data.map((item,index)=>{
            item.map((items)=>{
                this.obj.arr.push({
                    x:items.channel+1,
                    y:index+1,
                    z:items.rootNum+1,
                    value:items.temperature
                })
            })
        });
        let obj = this.obj;
        $(".box").css({
            width:_this.boxWidth*obj.x,
            height:_this.boxWidth*obj.z
        });
        $('.box .in-middle').css({
            width:_this.boxWidth*obj.x+50,
            height:_this.boxWidth*obj.z+50,
            transform: "translateY("+_this.boxWidth*obj.z/2+"px) rotateX(-90deg)"
        });
        $('.box .in-left').css({
            width:_this.boxWidth*obj.z+50,
            height:_this.boxWidth*obj.y,
            transform: `translateX(-${_this.boxWidth*obj.z/2}px) rotateY(-90deg)`
        });
        $('.box .in-top').css({
            width:_this.boxWidth*obj.x+50,
            height:_this.boxWidth*obj.y,
            transform: `translateZ(-${_this.boxWidth*obj.y/2+25}px)`
        });
        this.$nextTick(() => {
            $(".line").height(_this.boxWidth);
            $(".con").height(_this.boxWidth).width(_this.boxWidth);
            $(".con").css("lineHeight",_this.boxWidth+"px");
        });
    });
      document.onmousedown = function (e) {
        _this.flag = true;
        _this.old_x = e.pageX;
        _this.old_y = e.pageY;
        //console.log(old_x, old_y);
        /*原本所处的位置*/
      };
      document.onmouseup = function () {
        _this.flag = false;
        //鼠标抬起时结束
      };
      document.onmousemove = function (e) {
        if (_this.flag) {
          var _x = e.pageX - _this.old_x;
          var _y = e.pageY - _this.old_y;
          /*新位置减去老位置*/
          _this.RotateY += _x / 400;
          _this.RotateX += -(_y / 400);
          $('.box').css({
            transition: 'linear',
            transform: 'rotateX(' + _this.RotateX + 'deg) rotateY(' + _this.RotateY + 'deg) scale('+(1+_this.num*0.01) +')'
          });
        }
      };
      var scroolFun = (event)=>{
          event = event || window.event;
          var wheel = event.wheelDelta || event.detail;
          if(wheel<0){
              _this.num--;
          }else{
              _this.num++;
          }
          $(".box").css({
              transform:'rotateX(' + _this.RotateX + 'deg) rotateY(' + _this.RotateY + 'deg) scale('+(1+_this.num*0.01) +')'
          })
      };
      $(".content")[0].addEventListener('DOMMouseScroll',scroolFun,false);
      $(".content")[0].onmousewheel=scroolFun;
  },
  methods: {
    temperatureFun(value){
        let val = parseFloat(value);
        if(val<=-10){
            return this.color[0]
        }else if(-5>=val && val>-10){
            return this.color[1]
        }else if(0>=val && val>-5){
            return this.color[2]
        }else if(5>=val && val>0){
            return this.color[3]
        }else if(10>=val && val>5){
            return this.color[4]
        }else if(15>=val && val>10){
            return this.color[5]
        }else if(20>=val && val>15){
            return this.color[6]
        }else if(25>=val && val>20){
            return this.color[7]
        }else if(30>=val && val>25){
            return this.color[8]
        }else if(30<=val){
            return this.color[9]
        }
    },
    temperatureFun1(value){
        let val = parseFloat(value);
        if(val<=0){
            return '#b6e5e0';
        }else if(10>=val && val>0){
            return '#2ee3dd';
        }else if(20>=val && val>10){
            return '#ffdb33';
        }else if(30>=val && val>20){
            return '#ff8227';
        }else if(30<val){
            return '#ff2400';
        }
    },
    arrValue(x,y,z){//根据x，y，z查找值
        let value = 0
        this.obj.arr.map((item)=>{
            if(item.x==x && item.y==y && item.z==z){
                value = item.value
            }
        })
        return parseFloat(value);
    },
    conValue(x,y,z){
        //x+1 &&  z+1  && (x+1,z+1)
        return (this.arrValue(x,y,z) + this.arrValue(x+1,y,z) + this.arrValue(x,y,z+1) + this.arrValue(x+1,y,z+1))/4
    }

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    .content {
        padding-top: 200px;
        perspective: 1000px; /* 视距 */
        background:#666;
    }
    
    .box {
        position: relative;
        margin: 0 auto;
        width: 1000px;
        height: 300px;
        position: relative;
        color: #fffdf5;
        font-size: 36px;
        font-weight: bold;
        text-align: center;
        line-height: 100px;
        transform-style: preserve-3d; /* 默认 2D */
        /*不让鼠标选中文字*/
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transform: rotateZ(0deg) rotateX(0deg) rotateY(0deg);
    }
    
    
    

    .box .out-bottom {
        transform: translateY(100px) rotateX(-90deg);
    }
    
    
    
    .box .in-front {
        transform: translateZ(50px);
    }
    
    .box .in-back {
        transform: translateZ(-50px) rotateY(180deg);
    }
    
    .box .in-left {
        width:300px;
        height: 300px;
        border: 1px solid red;
        position: absolute;
        left:-50px;
        background-color: #fff;
        opacity: 0.2;
        transform: translateX(-150px) rotateY(-90deg);
    }
    
    .box .in-right {
        border: 1px solid red;
        position: absolute;
        background-color: #fff;
        opacity: 0.2;
        transform: translateX(10px) rotateY(90deg);
    }
    
    .box .in-top {
        width:1000px;
        height: 300px;
        border: 1px solid red;
        position: absolute;
        left:-25px;
        background-color: #fff;
        opacity: 0.2;
        transform: translateZ(-150px);
    }
    .box .head1 {
        width:1000px;
        height: 300px;
        border: 1px solid red;
        position: absolute;
        left:-25px;
        background-color: #fff;
        opacity: 0.2;
    }
    .box .head2 {
        width:1000px;
        height: 300px;
        border: 1px solid red;
        position: absolute;
        left:-25px;
        background-color: #fff;
        opacity: 0.2;
    }
    .box .in-middle {
        width:1000px;
        height: 300px;
        border: 1px solid red;
        position: absolute;
        left:-25px;
        bottom:-26px;
        background-color: #fff;
        opacity: 0.2;
        transform: translateY(150px) rotateX(-90deg);
    }
    .con{
        position: absolute;
        left:0;
        bottom:0;
        width:100px;
        height: 100px;
        font-size:12px;
        color:#fff;
        /* border:1px solid red; */
        transform:rotateX(-90deg) translateY(100px);
        background-color: aqua;
        text-align: center;
        opacity: 0.5;
        /* border-top:none; */
        /* border-bottom:none; */
    }
    .tit{
        position: absolute;
        z-index:1;
        left:-15px;
        bottom:-8px;
        width:30px;
        height: 16px;
        color:rgb(3, 1, 1);
        background-color: azure;
        font-size: 12px;
        line-height: 16px;
        transform:rotateX(90deg) translateY(-150px);
        text-align: center;
    }
    .line{
        width: 1px;
        height:100px;
        background-color: rgb(153, 151, 51);
        position: absolute;
        left:0;
        bottom:0;
    }
    .con img{
        height:100px;
    }
</style>



