import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import ck from '@/components/ck'
import ckwx from '@/components/ckwx'
import storehouse from '@/components/storehouse'
import storearea from '@/components/store-area'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: storehouse
    },{
      path: '/ck',
      name: 'ck',
      component: ck
    },{
      path: '/ckwx',
      name: 'ckwx',
      component: ckwx
    },{
      path: '/storehouse',
      name: 'storehouse',
      component: storehouse
    },{
      path: '/storearea',
      name: 'storearea',
      component: storearea
    }
  ]
})



