<template>
    <div>
        <el-form ref="form" :model="form" label-width="150px" style="background:#fff;">
            <el-form-item label="选择列：">
                <el-radio-group v-model="form.x">
                    <el-radio label="">全部</el-radio>
                    <el-radio v-for="item in obj.x" :label="parseInt(item)" :key="'lie' + item">第{{item}}列</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="选择行：">
                <el-radio-group v-model="form.z">
                    <el-radio label="">全部</el-radio>
                    <el-radio v-for="item in obj.z" :label="parseInt(item)" :key="'hang' + item">第{{item}}行</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="选择层：">
                <el-radio-group v-model="form.y">
                    <el-radio label="">全部</el-radio>
                    <el-radio v-for="item in obj.y" :label="parseInt(obj.y-item+1)" :key="'ceng' + item">第{{item}}层</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item label="展示方式：">
                <el-radio-group v-model="form.type">
                    <el-radio label="0">整仓</el-radio>
                    <el-radio label="1">周边</el-radio>
                    <el-radio label="2">堆内</el-radio>
                </el-radio-group>
            </el-form-item>
            <el-form-item>
            </el-form-item>
            </el-form>
        <div class="content">
            <div class="box" id="box">
                <div class="in-middle">
                    <div v-for="item in obj.x" :key="'middle'+item" class="scale-x" :style="'left:'+ (boxWidth*(item-1)) +'px;'">第{{item}}列</div>
                    <div v-for="item in obj.z" :key="'middle'+item" class="scale-z" :style="'right:'+ (0-boxWidth) +'px;top:'+ (boxWidth*(item-0.75)) +'px;'">第{{obj.z-item+1}}行</div>
                    <div class="gridlines" v-for="item in obj.z+1" :key="'grmiddle'+item" :style="'width:100%;top:'+ ((item-1)*boxWidth) +'px;'"></div>
                    <div class="gridlines" v-for="item in obj.x+1" :key="'grmiddleleft'+item" :style="'height:100%;width:1px;bottom:0px;left:'+ ((item-1)*boxWidth) +'px;'"></div>
                </div>
                <div class="in-left">
                    <div v-for="item in obj.y" :key="'left'+item" class="scale-y" :style="'left:'+ (boxWidth*(obj.z-0.5)) +'px;bottom:'+ (boxWidth*(item-0.75)) +'px;transform: rotateY(90deg) translateX('+ (0-boxWidth/1) +'PX);'">第{{obj.y-item+1}}层</div>
                    <div class="gridlines" v-for="item in obj.y" :key="'grleft'+item" :style="'width:100%;top:'+ ((item-1)*boxWidth) +'px;'"></div>
                    <div class="gridlines" v-for="item in obj.z" :key="'grleftleft'+item" :style="'height:100%;width:1px;bottom:0px;left:'+ ((item-1)*boxWidth) +'px;'"></div>
                </div>
                <div class="in-top">
                    <div class="gridlines" v-for="item in obj.y+1" :key="'grtop'+item" :style="'width:100%;top:'+ ((item-1)*boxWidth) +'px;'"></div>
                    <div class="gridlines" v-for="item in obj.x+1" :key="'grtopleft'+item" :style="'height:100%;width:1px;bottom:0px;left:'+ ((item-1)*boxWidth) +'px;'"></div>
                </div>
                <div id="tit">
                    <span class="tit" v-for="v in obj.arr" :key="'dian'+v.x+v.y+v.z"
                    v-show="((form.x=='' && form.y=='' && form.z=='') || (form.x == v.x) || (form.z == v.z) || (form.y == v.y)) && ((form.type=='0') || (form.type=='1' && ((form.x != '' && (v.y == 1 || v.y == obj.y || v.z == 1 || v.z == obj.z)) || (form.z != '' && (v.y == 1 || v.y == obj.y || v.x == 1 || v.x == obj.x)) || ((form.x == '' && form.y == '' && form.z == '') && (v.x == 1 || v.x == obj.x || v.z == 1 || v.z == obj.z || v.y == 1 || v.y == obj.y)) || (form.y != '' && (v.x == 1 || v.x == obj.x || v.z == 1 || v.z == obj.z)))) || (form.type=='2' && ((form.x != '' && (v.y > 1 && v.y < obj.y && v.z > 1 && v.z < obj.z)) || (form.z != '' && (v.y > 1 && v.y < obj.y && v.x > 1 && v.x < obj.x)) || (form.y != '' && (v.x > 1 && v.x < obj.x && v.z > 1 && v.z < obj.z)) || ((form.x == '' && form.y == '' && form.z == '') && (v.x > 1 && v.x < obj.x && v.y > 1 && v.y < obj.y && v.z > 1 && v.z < obj.z)))))"
                    :x="v.x" :y="v.y" :z="v.z" :style="'left:' + ((v.x-0.5)*boxWidth-15) +'px;transform:rotateX(90deg) translateY('+((v.z)*boxWidth-boxWidth*obj.z/2-15)+'px) translateZ('+((v.y-0.5)*boxWidth)+'px);'">
                      <span class="substance" :style="'height: 16px;background-color:'+temperatureFun(v.value)+';'">{{v.value}}</span>
                      <span class="line" v-if="v.y!=1" :style="'width:'+ (boxWidth-2) +'px;'" v-show="form.y=='' && form.type == '0'"></span>
                    </span>
                </div>
            </div>
        </div>
    </div>
</template>
<script>

export default {
  name: "three",
  data() {
    return {
      obj:{ 
          x:10, //列
          y:3, //层
          z:3, //行
          arr:[]
      },
      boxWidth:50, //容器长度单位
      RotateY:10,
      RotateX:-10,
      old_x:0,
      old_y:0,
      num:0,
      flag:false,
      boxStyle:'',
      maxTemperature:35,
      minTemperature:10,
      form:{
          x:'',
          y:'',
          z:'',
          type:"2"
      },
      color:["#C00000",'#f74015','#1082f9']
    };
  },
  created(){

  },
  mounted() {
    // const axios = require('axios');

    // axios.get('http://39.100.86.127:7800/api/temperature/echarts').then((response) => {
      
    // })
    let _this = this;
    $("body").append("<div style='width:100%;height:100%;background:#666;position:fixed;left: 0;top: 0;z-index: -1;'></div>")
    const axios = require('axios');
        axios.get('http://39.100.86.127:7800/api/temperature/echarts').then((response) => {
        this.data = response.data.data;
        this.obj.y = response.data.data.length; //层
        this.obj.x = response.data.data[0][response.data.data[0].length-1].channel+1; //列
        this.obj.z = response.data.data[0][response.data.data[0].length-1].rootNum+1; //行
        let x = 0;
        this.obj.arr = [];
        response.data.data.map((item,index)=>{
            item.map((items)=>{
                this.obj.arr.push({
                    x:items.channel+1,
                    y:_this.obj.y-index,
                    z:items.rootNum+1,
                    value:items.temperature
                })
            })
        });
        let obj = this.obj;
        this.$nextTick(() => {
          $(".box").css({
                width:_this.boxWidth*obj.x,
                height:_this.boxWidth*obj.y
            });
            $('.box .in-middle').css({
                width:_this.boxWidth*obj.x,
                height:_this.boxWidth*obj.z,
                transform: "translateY("+_this.boxWidth*obj.z/2+"px) rotateX(-90deg)"
            });
            $('.box .scale-x').css({
                width:_this.boxWidth,
                height:30,
            });
            $('.box .scale-y').css({
                width:_this.boxWidth,
                height:30,
            });
            $('.box .scale-z').css({
                width:_this.boxWidth,
                height:30,
            });
            $('.box .in-left').css({
                width:_this.boxWidth*obj.z,
                height:_this.boxWidth*obj.y,
                transform: `translateX(-${_this.boxWidth*obj.z/2}px) rotateY(-90deg)`
            });
            $('.box .in-top').css({
                width:_this.boxWidth*obj.x,
                height:_this.boxWidth*obj.y,
                transform: `translateZ(-${_this.boxWidth*obj.z/2}px)`
            });
            // $(".line").height(_this.boxWidth);
            // $(".con").height(_this.boxWidth).width(_this.boxWidth);
            // $(".con").css("lineHeight",_this.boxWidth+"px");
        });
    });
      $(".content")[0].onmousedown = function (e) {
        _this.flag = true;
        _this.old_x = e.pageX;
        _this.old_y = e.pageY;
        //console.log(old_x, old_y);
        /*原本所处的位置*/
      };
      $(".content")[0].onmouseup = function () {
        _this.flag = false;
        //鼠标抬起时结束
      };
      $(".content")[0].onmousemove = function (e) {
        if (_this.flag) {
          var _x = e.pageX - _this.old_x;
          var _y = e.pageY - _this.old_y;
          /*新位置减去老位置*/
          _this.RotateY += _x / 400;
          _this.RotateX += -(_y / 400);
          $('.box').css({
            transition: 'linear',
            transform: 'rotateX(' + _this.RotateX + 'deg) rotateY(' + _this.RotateY + 'deg) scale('+(1+_this.num*0.01) +')'
          });
        }
      };
      var scroolFun = (event)=>{
          event = event || window.event;
          var wheel = event.wheelDelta || event.detail;
          if(wheel<0){
              _this.num--;
          }else{
              _this.num++;
          }
          $(".box").css({
              transform:'rotateX(' + _this.RotateX + 'deg) rotateY(' + _this.RotateY + 'deg) scale('+(1+_this.num*0.01) +')'
          })
          return false;
      };
      $(".content")[0].addEventListener('DOMMouseScroll',scroolFun,false);
      $(".content")[0].onmousewheel=scroolFun;
  },
  methods: {
    temperatureFun(value){
        let val = parseFloat(value);
        if(val>=this.maxTemperature){
            return this.color[0]
        }else if(this.maxTemperature>val && val>=this.minTemperature){
            return this.color[1]
        }else{
            return this.color[2]
        }
    },
    temperatureFun1(value){
        let val = parseFloat(value);
        if(val<=0){
            return '#b6e5e0';
        }else if(10>=val && val>0){
            return '#2ee3dd';
        }else if(20>=val && val>10){
            return '#ffdb33';
        }else if(30>=val && val>20){
            return '#ff8227';
        }else if(30<val){
            return '#ff2400';
        }
    },
    arrValue(x,y,z){//根据x，y，z查找值
        let value = 0
        this.obj.arr.map((item)=>{
            if(item.x==x && item.y==y && item.z==z){
                value = item.value
            }
        })
        return parseFloat(value);
    },
    conValue(x,y,z){
        //x+1 &&  z+1  && (x+1,z+1)
        return (this.arrValue(x,y,z) + this.arrValue(x+1,y,z) + this.arrValue(x,y,z+1) + this.arrValue(x+1,y,z+1))/4
    }

  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    *{
        margin: 0;
        padding: 0;
    }
    .content {
        padding-top: 200px;
        perspective: 1000; /* 视距 */
        background:#666;
        height: 1000px;
    }
    
    .box {
        position: relative;
        margin: 0 auto;
        color: #fffdf5;
        font-size: 36px;
        font-weight: bold;
        text-align: center;
        line-height: 100px;
        transform-style: preserve-3d; /* 默认 2D */
        /*不让鼠标选中文字*/
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transform: rotateZ(0deg) rotateX(0deg) rotateY(0deg);
    }
    
    
    

    .box .out-bottom {
        transform: translateY(100px) rotateX(-90deg);
    }
    
    
    
    .box .in-front {
        transform: translateZ(50px);
    }
    
    .box .in-back {
        transform: translateZ(-50px) rotateY(180deg);
    }
    
    .box .in-left {
        position: absolute;
        transform-style: preserve-3d; /* 默认 2D */
        background-color: rgba(249,204,157,0.2);
    }
    
    .box .in-right {
        position: absolute;
        background-color: rgba(249,204,157,0.2);
        /* transform: translateX(10px) rotateY(90deg); */
    }
    
    .box .in-top {
        position: absolute;
        background-color: rgba(249,204,157,0.2);
    }
    .box .in-middle {
        position: absolute;
        left:0;
        bottom:0;
        background-color: rgba(249,204,157,0.2);
        /* transform: translateY(150px) rotateX(-90deg); */
    }
    .box .gridlines{
      position: absolute;
      height: 1px;
      background-color: rgba(249,204,157,0.8);
    }
    .box .scale-x{
      position: absolute;
      text-align: center;
      font-size:16px;
      line-height:30px;
      color:rgb(8, 1, 1);
      top:-30px;
      font-weight: normal;
      transform:rotateX(-180deg);
    }
    .box .scale-y{
      position: absolute;
      text-align: center;
      font-size:16px;
      line-height:30px;
      color:rgb(8, 1, 1);
      font-weight: normal;
    }
    .box .scale-z{
      position: absolute;
      text-align: center;
      font-size:16px;
      line-height:30px;
      color:rgb(8, 1, 1);
      transform:rotateX(-180deg);
      font-weight: normal;
    }
    .con{
        position: absolute;
        left:0;
        bottom:0;
        width:100px;
        height: 100px;
        font-size:12px;
        color:#fff;
        /* border:1px solid red; */
        transform:rotateX(-90deg) translateY(100px);
        background-color: aqua;
        text-align: center;
        opacity: 0.5;
        /* border-top:none; */
        /* border-bottom:none; */
    }
    .tit{
        position: absolute;
        z-index:1;
        left:0;
        bottom:0;
        width:30px;
        height: 1px;
        /* transform:rotateX(90deg) translateY(-150px); */
        transform-style: preserve-3d;
    }
    .tit .substance{
        position: absolute;
        left:0;
        width:100%;
        height:100%;
        bottom:0;
        color:rgb(3, 1, 1);
        font-size: 12px;
        line-height: 16px;
        text-align: center;
    }
    .tit .line{
        height:1px;
        background-color: rgb(8, 1, 1);
        position: absolute;
        left:0;
        bottom:0;
        transform-origin: 0 0;
        transform:rotateY(90deg) translateZ(15px) translateY(-8px);
    }
    .con img{
        height:100px;
    }
</style>



